
import count from '@/components/countup'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name: '',
components: {
count,
XButton,
XNumber,
Group,
Datetime,
PopupPicker
},
data() {
return {
endVal: 500,
value: '',
visibility: true,
isShowPopup: false,
list: [],
nameList: [[1, 2, 3]],
isShow: false,
isShowButton: false,
isShowFather: false
}
},

methods: {
onFatherClick(event) {
console.log('father click', event.target)
},
onChildClick(e) {
e.stopPropagation()
console.log('child1 click')
},
onButtonClick() {
this.isShowButton = !this.isShowButton
},
onTextClick() {

this.isShow = !this.isShow
}
},
mounted() {
this.$refs.father.addEventListener('click', this.onFatherClick, false)

this.$refs.child.addEventListener('click', this.onChildClick, false)
this.$refs.link.addEventListener('click', e => {
e.preventDefault()
})
},
sockets: {
connect: function() {
console.log('socket connected')
}
}
}



